<template>
  <div id="home">
    <nav-bar class="home-nav">
      <template #center>
        <div class="home-center">购物街</div>
      </template>
    </nav-bar>
    <tab-control :title="['流行','新款','精选']" @tabClick="tabClick" 
                 v-if="isTabControlShow" class="tab-control" ref="tabControl2" />
    <scroll class="home-wrap" ref="scroll" :probe-type="3" @scroll="backtopShow"
            :pull-up-load="true" @pullingUp="loadMore">
      <home-swiper :banners="banners" @swiperImgLoad="swiperImgLoad" ></home-swiper>
      <home-recommend :recommends="recommends"></home-recommend>
      <home-feature></home-feature>
      <tab-control :title="['流行','新款','精选']" @tabClick="tabClick" ref="tabControl1"/>
      <goods-list :goods-list="goods[currentType].list"></goods-list>
    </scroll>
    <back-top @click.native="backTopClick" v-show="isBackTopShow" />
  </div>
</template>
<script>
import HomeSwiper from './childComps/HomeSwiper'
import HomeRecommend from './childComps/HomeRecommend'
import HomeFeature from './childComps/HomeFeature'

import NavBar from 'components/common/navBar/NavBar'
import TabControl from 'components/content/tabControl/TabControl'
import GoodsList from 'components/content/goods/GoodsList'
import Scroll from 'components/common/scroll/Scroll'
import BackTop from 'components/content/backTop/BackTop'

import { getHomeMultiData,getHomeData } from 'network/home'

import { itemImgListener,backTopShow } from 'commons/mixin'

export default {
  name: 'Home',
  components: {
    HomeSwiper,
    HomeRecommend,
    HomeFeature,
    NavBar,
    TabControl,
    GoodsList,
    Scroll,
    BackTop
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goods: {
        'pop': {page: 1, list: []},
        'new': {page: 1, list: []},
        'sell': {page: 1, list: []}
      },
      currentType: 'pop',
      offsetTop: 0,
      isTabControlShow: false,
      saveY: 0
    }
  },
  mixins: [itemImgListener,backTopShow],
  created(){
    this.getHomeMultiData()
    this.getHomeDatas('pop')
    this.getHomeDatas('new')
    this.getHomeDatas('sell') 
  },
  activated(){
    //  为了让页面位置不变，记录Home页面切换时的位置
    this.$refs.scroll.ScrollTo(0, this.saveY)
  },
  deactivated(){
    // 根据记录的位置返回
    this.saveY = this.$refs.scroll.scroll.y 
    // 取消bus监听图片刷新
    this.$bus.$off("itemImgLoad", this.itemImgListener)
  },
  methods: {
    /**
     * 事件监听相关方法
     * 
     */
    // 1.点击控制条显示对应商品列表
    tabClick(index){
      switch(index){
        case 0:
          this.currentType = 'pop';
          break;
        case 1:
          this.currentType = 'new';
          break;
        case 2:
          this.currentType = 'sell';
          break;
      }
      this.$refs.tabControl1.currentIndex = index
      this.$refs.tabControl2.currentIndex = index
    },
    // 3.控制向上返回按钮出现或消失
    backtopShow(position){
      const positionY = -position.y
      this.isBackTopShow = positionY > 1000
      this.isTabControlShow = positionY > this.offsetTop
    },
    // 4.上拉加载更多
    loadMore(){
      this.getHomeDatas(this.currentType)
    },
    // 5.
    swiperImgLoad(){
      this.offsetTop = this.$refs.tabControl1.$el.offsetTop  
    },
    
    /**
     * 网络请求相关方法
     */
    // 1.获取首页轮播图及推荐数据
    getHomeMultiData(){
      getHomeMultiData()
      .then(res => {
        this.banners = res.data.banner.list
        this.recommends = res.data.recommend.list
      })
      .catch(err => {
        console.log(err);
      })
    },
    // 2.获取商品列表数据
    getHomeDatas(type){
      getHomeData(type,this.goods[type].page)
      .then(res => {
        this.goods[type].list.push(...res.data.list)
        this.goods[type].page += 1
        this.$refs.scroll.FinishPullUp()
      })
      .catch(err => {
        console.log(err);
      })
    }
  }
}
</script>
<style scoped>
#home {
  /* padding-top: 44px; */
  height: 100vh;
}
.home-nav {
  background-color: var(--color-high-text);
  color: #fff;
  font-size: 18px;
}

.home-wrap {
  height: calc(100% - 93px);
  margin-top: 44px;
}

.tab-control {
  position: relative;
  top: 44px;
  z-index: 99;
}


</style>