<template>
  <div class="tab-control">
    <div class="control-item" 
         v-for="(item,index) in title" :key="index"
         @click="itemClick(index)">
      <span :class="{active: currentIndex === index}">{{item}}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'TabControl',
  components: {},
  data() {
    return {
      currentIndex: 0
    }
  },
  props: {
    title: {
      type: Array,
      default(){
        return []
      }
    }
  },
  methods: {
    itemClick(index){
      this.currentIndex = index;
      this.$emit('tabClick',index)
    }
  }
}
</script>
<style scoped>
.tab-control {
  display: flex;
  text-align: center;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  background-color: #fff;
}
.control-item {
  flex: 1;
}
.control-item span {
  padding: 5px;
}
.active {
  color: var(--color-high-text);
  border-bottom: 1px solid var(--color-high-text);
}

</style>